.date_popup {
    width: 90px;
    height: 30px;
    margin-left: 90px;
    top:-20px;
    background: linen;
    position: relative;
    border-radius: 10px;
}
.date_popup:before {
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    right: 90%;
    top: 3px;
    border-top: 12px solid transparent;
    border-right: 24px solid linen;
    border-bottom: 12px solid transparent;
}
